/**
 * @class ExampleComponent
 */

import * as React from 'react'
import { useRoute } from 'react-router5'
import BreadcrumbsRender from './render'
import { BreadcrumbsRenderProps } from './typings'

type paths = {
  path: string
  name: string
}

function Breadcrumbs({
  removeCrumb,
  forward,
  translationHandler = (text: string) => text,
  icons,
}: BreadcrumbsRenderProps) {
  const {
    route: { name, params },
  } = useRoute()

  const routeNameToArray = (acc: Array<paths>, curr: string, idx: number) => {
    const getString = (x: string) => {
      const val = x === '/' ? 'name' : 'path'
      return !(acc.length > 0)
        ? curr
        : curr === 'tab'
        ? acc[idx - 1][val] + x + params.tab
        : acc[idx - 1][val] + x + curr
    }

    return [
      ...acc,
      {
        path: `${getString('.')}`,
        name: `${getString('/')}`,
      },
    ]
  }
  const pathsArray = name.split('.').reduce(routeNameToArray, [])

  const filteredPaths = pathsArray.filter(
    (el: { path: string }) =>
      !(removeCrumb !== undefined && removeCrumb.includes(el.path, 0))
  )

  return (
    <BreadcrumbsRender
      crumbs={filteredPaths}
      params={params}
      forwarding={forward}
      translationHandler={translationHandler}
      icons={icons}
    />
  )
}

export { Breadcrumbs }
