import * as React from 'react'
import { Link, useRoute } from 'react-router5'
import { HomeIcon } from './assets/home'
import { ArrowIcon } from './assets/arrow'

type Crumb = {
  path: string
  name: string
  parameters: { [name: string]: string }
}

export default function BreadcrumbsRender({
  crumbs,
  forwarding,
  translationHandler,
  icon,
  activeLinkClass = 'text-blue-700 hover:underline mx-2',
  currentPageClass = 'text-gray-500 ml-2',
  wrapperClass = 'flex items-baseline text-xl my-10 whitespace-no-wrap',
}: any) {
  const {
    route: { params, path },
    router: { forward, getDependencies },
  } = useRoute()

  function Next() {
    return <ArrowIcon {...icon} />
  }

  const handleClick = () =>
    forwarding ? forward(forwarding.from, forwarding.to) : () => null
  const dependencies = getDependencies()

  function Crumbs() {
    return crumbs.map((crumb: Crumb, idx: number) => {
      const notLast = idx < crumbs.length - 1
      const currentCrumb = dependencies.find(
        (el: { name: string }) => el.name === crumb.path
      )

      return (
        crumb.path !== '@@router5/UNKNOWN_ROUTE' && (
          <React.Fragment key={idx}>
            {notLast ? (
              translationHandler(`${currentCrumb.crumb || crumb.name}`) && (
                <>
                  <Link
                    className={activeLinkClass}
                    routeName={crumb.path}
                    routeParams={{ ...params }}
                    routeOptions={{ reload: true }}
                    onClick={handleClick}
                  >
                    {translationHandler(`${currentCrumb.crumb || crumb.name}`)}
                  </Link>
                  <Next />
                </>
              )
            ) : (
              <span className={currentPageClass}>
                {translationHandler(`${currentCrumb.crumb || crumb.name}`)}
              </span>
            )}
          </React.Fragment>
        )
      )
    })
  }

  return (
    path !== '/' && (
      <nav className={wrapperClass} style={{ textTransform: 'capitalize' }}>
        <HomeIcon {...icon} />
        <Link className={`${activeLinkClass}`} routeName={'home'}>
          {translationHandler('Home')}
        </Link>
        <Next />
        <Crumbs />
      </nav>
    )
  )
}
